import styled from 'styled-components';

const Card = styled.div`
  background-color: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  border-radius: 12px;
  display: inline-block;
  width: 100%;
  .icon {
    width: 16px;
    height: 16px;
  }
`;

export default Card;
